import "vant/es/checkbox/style/less";
import _Checkbox from "vant/es/checkbox";
import "vant/es/radio/style/less";
import _Radio from "vant/es/radio";
import "vant/es/radio-group/style/less";
import _RadioGroup from "vant/es/radio-group";
import "vant/es/popup/style/less";
import _Popup from "vant/es/popup";
import "vant/es/button/style/less";
import _Button from "vant/es/button";
import { computed, defineComponent, ref } from 'vue';
import { useStore } from 'vuex';
import { useRoute } from 'vue-router';
export default defineComponent({
  name: 'PubSection',
  props: {
    showPop: {
      type: Boolean,
      required: true
    },
    banks: {
      type: Array,
      required: true
    }
  },
  components: {
    [_Button.name]: _Button,
    [_Popup.name]: _Popup,
    [_RadioGroup.name]: _RadioGroup,
    [_Radio.name]: _Radio,
    [_Checkbox.name]: _Checkbox
  },

  setup(props) {
    const store = useStore();
    const route = useRoute();
    const useChecked = ref(1);
    const bankRadio = ref(0);

    const sign = () => {
      let params = {
        data: {
          name: route.params.name,
          idno: route.params.idno,
          tele: route.params.tele,
          payType: props.banks[bankRadio.value].payType,
          firstYxf: useChecked.value ? '1' : '0'
        }
      };
      store.dispatch('publicModule/tSign', params);
    };

    const toProtoHref = () => {
      const {
        agreementUrl
      } = props.banks[bankRadio.value];
      self.location.href = agreementUrl.startsWith('http://') ? agreementUrl : `http://${agreementUrl}`;
    };

    const hosChange = val => bankRadio.value = val;

    return {
      sign,
      useChecked,
      bankRadio,
      hospitals: computed(() => store.state.publicModule.hosips),
      toProtoHref,
      hosChange
    };
  }

});